export default {
  data() {
    return {
      username: '',
      password: '',
      forget_pass: true,
      loading: false,
      otp: '',
      reset_pass: false
    }
  },
  methods: {
    async login(e) {
      this.loading = true;
      e.preventDefault();
      if (this.username.trim() === '') {
        this.loading = false;
        return this.$notify({
          title: 'لطفا شماره موبایل یا ایمیل خود را وارد کنید.',
          type: 'error'
        });
      }
      if (this.password.trim() === '') {
        this.loading = false;
        return this.$notify({
          title: 'لطفا رمزعبور خود را وارد کنید.',
          type: 'error'
        });
      }
      const data = {
        'username': this.username,
        'password': this.password,
      }
      const log = await this.$axios.post('login', data)

      if (log.status === 200) {
        this.loading = false
        return this.$notify({
          title: log.data.message,
          type: 'error'
        });
      }
      if (log.status === 201) {
        localStorage.setItem('117115101114', JSON.stringify(log.data.user));
        localStorage.setItem('116111107101110', log.data.token);
        this.$notify({
          title: 'شما باموفقیت وارد حساب کاربری خود شدید!',
          type: 'success'
        });
        if (log.data.user.is_customer === 1) window.location = 'tutorial-videos';
        else this.$router.go(-1);

      }
      this.loading = false;
    },
    async sendOtp(e) {
      this.loading = true;
      e.preventDefault();
      if (this.username.trim() === '') {
        this.loading = false;
        return this.$notify({
          title: 'لطفا شماره موبایل یا ایمیل خود را وارد کنید.',
          type: 'error'
        });
      }
      const data = {
        'username': this.username,
      }
      const log = await this.$axios.post('forget_password', data)

      console.log('lk' + log)
      if (log.status === 200) {
        this.loading = false
        return this.$notify({
          title: log.data.message,
          type: 'error'
        });
      }
      if (log.status === 201) {
        this.reset_pass = true
        this.$notify({
          title: log.data.message,
          type: 'success'
        });
      }
      this.loading = false;
    },
    async resetPassword(e) {
      this.loading = true;
      e.preventDefault();
      if (this.otp.trim() === '') {
        this.loading = false;
        return this.$notify({
          title: 'لطفا رمزعبور یکبار مصرف را وارد کنید.',
          type: 'error'
        });
      }
      const data = {
        'otp': this.otp,
        'username': this.username,
      }
      const log = await this.$axios.post('reset_password', data)

      if (log.status === 200) {
        this.loading = false
        return this.$notify({
          title: log.data.message,
          type: 'error'
        });
      }
      if (log.status === 201) {
        localStorage.setItem('117115101114', JSON.stringify(log.data.user));
        localStorage.setItem('116111107101110', log.data.token);
        this.$notify({
          title: 'شما باموفقیت وارد حساب کاربری خود شدید، لطفا رمزعبور خود را تغییر دهید.!',
          type: 'success'
        });
        window.location = 'my-account';
      }
      this.loading = false;
    }
  }
  , computed: {
    forgetPassword() {
      return this.forget_pass
    }
  }
}
